"use client";

import Link from "next/link";
import { Meal } from "@/types";
import { use } from "react";

interface MealIngredientsProps {
  mealPromise: Promise<Meal>;
}

export default function MealIngredients({ mealPromise }: MealIngredientsProps) {
  const meal = use(mealPromise);

  const ingredients = Object.keys(meal)
    .filter((key) => key.startsWith("strIngredient"))
    .map((key, i) => {
      const ingredient = meal[key as keyof Meal];
      const measure = meal[`strMeasure${i + 1}` as keyof Meal];
      return { ingredient, measure };
    })
    .filter((item) => item.ingredient && item.ingredient.trim());

  return (
    <div className="mb-8">
      <h2 className="text-xl font-semibold mb-4">Ingredients</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
        {ingredients.map((item, index) => (
          <Link
            key={index}
            href={`/?ingredient=${item.ingredient}`}
            className="flex items-center gap-2 p-3 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
          >
            <span className="font-medium">{item.ingredient}</span>
            <span className="text-gray-600">{item.measure}</span>
          </Link>
        ))}
      </div>
    </div>
  );
}
