"use client";

import Link from "next/link";
import { Meal } from "@/types";
import { use } from "react";

interface CategoryMealsProps {
  categoryPromise: Promise<string>;
  mealsPromise: Promise<Meal[]>;
}

export default function CategoryMeals({
  categoryPromise,
  mealsPromise,
}: CategoryMealsProps) {
  const category = use(categoryPromise);
  const meals = use(mealsPromise);

  return (
    <div className="lg:w-1/3">
      <div className="bg-gray-50 p-6 rounded-lg">
        <h2 className="text-xl font-semibold mb-4">Other {category} Meals</h2>
        <ul className="space-y-2">
          {meals.map((meal) => (
            <li key={meal.idMeal}>
              <Link
                href={`/meal/${meal.idMeal}`}
                className="text-gray-400 hover:underline"
              >
                {meal.strMeal}
              </Link>
            </li>
          ))}
        </ul>
        <Link
          href={`/?category=${category}`}
          className="block text-center mt-4 text-blue-600 hover:underline"
        >
          View all {category} meals
        </Link>
      </div>
    </div>
  );
}
